
@media(max-width:1290px){
   .intro-content{
      padding-left: 10px;
   }
   .about-content{
      margin-left: 20px;
   }
   .about-content__subtitle{
      padding-right: 10px;
   }
}
@media(max-width:1080px){
   .intro-content__title{
      font-size: 35px;
      line-height: 40px;
   }
   .intro-img{
      width: 50%;
   }
   .about-content__subtitle{
      font-size: 15px;
   }
   .about-img{
      width: 50%;
   }
   .blog-card__content--main{
      padding-left: 10px;
   }
}
@media(max-width:850px){
   .contacts{
      display: none;
   }
   .footer-content{
      padding-top: 20px;
   }
   .intro .wrapper{
      display: block;
   }
   .intro-img{
      width: 100%;
   }
   .intro-content{
      padding-top: 40px;
      text-align: center;
   }
   .intro-content__title{
      display: inline-block;
   }
   .intro-content__subtitle{
      display: inline-block;
   }
   .intro-content__btn{
      display: block;
      margin: 0 auto 50px;
   }
   .about .wrapper{
      display: block;
   }
   .about-img{
      width: 100%;
   }
   .about-content{
      padding-top: 40px;
      text-align: center;
   }
   .about-content__subtitle{
      display: inline-block;
   }
   .about-content__btn{
      display: block;
      margin: 0 auto;
   }
   .about{
      padding-bottom: 20px;
   }
   .services-blocks{
      display: block;
   }
   .services-block{
      width: 70%;
      margin: 0 auto;
      margin-bottom: 10px;
   }
   .start .wrapper{
      flex-wrap: wrap;
   }
   .start-content{
      margin: 0 auto;
   }
   .start-content__subtitle{
      margin-bottom: 15px;
   }
   .start-button{
      margin: 0 auto;
   }
   .start-wrapper{
      padding-top: 50px;
   }
   .blog-card__title--main{
      font-size: 40px;
      line-height: 48px;
   }
   .blog-card__wrapper{
      display: block;
   }
   .blog-card__img--main{
      width: 100%;
   }
   .blog-card__text--main{
      padding: 35px 0 27px;
   }
   .blog-card--main{
      padding-bottom: 15px;
   }
   .blog-cards{
      display: block;
   }
   .blog-card{
      max-width: 380px;
      margin: 0 auto;
      text-align: center;
      margin-bottom: 15px;
   }
   .blog-card__title{
      padding: 0 0 16px;
   }
   .blog-card__text{
      padding: 0 0 16px;
   }
   .blog-card__link{
      padding: 0;
   }
}
@media(max-width:601px){
   .header .wrapper{
      flex-wrap: wrap;
   }
}
@media(max-width:460px){
   .intro-content{
      padding-top: 70px;
   }
   .menu-list{
      display: none;
      position: absolute;
      top: 150px;
      text-align: center;
      background-color: #fafafa;
      width: 100%;
      left: 0;
      & li a{
         margin-right: 0;
         display: block;
         padding: 15px 0;
      }
   }
   .menu-burger{
      width: 30px;
      height: 20px;
      position: relative;
      & span{
         position: absolute;
         top: 9px;
         background-color: #000;
         width: 100%;
         height: 3px;
      }
      &::after, &::before{
         content: "";
         background-color: #000;
         width: 100%;
         height: 3px;
         position: absolute;
      }
      &:after{
         top: 0;
      }
      &::before{
         bottom: 0;
      }
   }
   .about-content__subtitle{
      font-size: 13px;
   }
   .services-title{
      padding: 40px 0 25px;
      font-size: 25px;
      line-height: 32px;
   }
   .services-subtitle{
      font-size: 15px;
      margin-bottom: 30px;
   }
}