html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html,
body {
  margin: 0;
  height: 100%;
  position: relative;
  font-size: 18px;
  color: #000;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-style: normal;
  scroll-behavior: smooth;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.wrapper{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header-container{
  max-width: 1110px;
  margin: 0 auto;
  padding: 47px 15px;
}

.intro-container{
  max-width: 1280px;
  margin-left: auto;
}

.about-container{
  max-width: 1280px;
}

.services-container{
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

.start-container{
  max-width: 990px;
  padding: 0 15px;
  margin: 0 auto;
}

.blog-container{
  max-width: 1190px;
  margin: 0 auto;
  padding: 0 15px;
}

.blog-card__wrapper{
  max-width: 970px;
  align-items: flex-start;
}

.contacts-container{
  max-width: 1030px;
  padding: 0 15px;
  margin: 0 auto;
}

.contacts-menu__container{
  max-width: 614px;
  margin: 0 auto;
}

.contacts-social__container{
  max-width: 400px;
  margin: 0 auto;
}